.projectMeta {
  margin: 0.5rem auto 0 0;
  padding: 14px 20px 20px 20px !important;
  border: 5px solid #323433;
  background-color: #eaeaea;
}

.projectMeta .projectMetaLeft {
  margin: 0;
}

.projectMeta .projectMetaRight {
  margin-top: 5px;
  text-align: center;
  font-size: 20px;
}

#projectNav {
  border-top: 3px solid #323433;
  border-bottom: 3px solid #323433;
  background-color: transparent;
  margin-bottom: 20px;
}

#projectNav li a {
  color: #323433;
  font-size: 20px;
  margin: 2px;
}

#projectNav li a:hover {
  background-color: transparent;
  -webkit-text-stroke-width: 0.2px;
  -webkit-text-stroke-color: #fdf0b5;
}


