.card {
  word-wrap: break-word;
}

.card .projectCard {
  border: 5px solid #323433;
  background-color: #eaeaea;
}

.card .projectCard p {
  margin: 1rem 0 1rem 0;
  color: #323433;
}

.card:hover {
  -webkit-transform: scale(1.03);
          -ms-transform: scale(1.03);
      transform: scale(1.03);
  cursor: pointer;
}


